form {
    display: flex;
    padding: 20px;
}

@media (max-width:768px) {
    .form {
        flex-direction: column;
    }
}

.form__button {
    color: white;
    background-color: hsl(180, 100%, 25%);
    border: none;
    padding: 10px;
    margin-left: 20px;
}

.form__button:hover {
    background-color: hsl(180, 100%, 30%); 
    transform: scale(1.1);
    transition: 0.5s;
}

@media (max-width:768px) {
    .form__button {
        margin-top: 10px;
        margin-left: 0;
    }
}

.form__button:active {
    background-color: hsl(180, 100%, 35%); 
}

.form__input {
    flex-grow: 1;
    padding: 10px;
    border: 1px solid #ddd;
}

.form__input:active {
    border: solid 1px #000;
}