.buttons__button {
    background: transparent;
    border: none;
    margin: 0 0 0 20px;
    cursor: pointer;
    color: hsl(180, 100%, 25%);
    transition: color 0.3s;
}
@media (max-width: 767px) {
    .buttons__button {
        flex-basis: 100%;
        margin: 12px;
    }
}


.buttons__button:hover {
    color: hsl(180, 100%, 35%);
}


.buttons__button:disabled {
    color: #ccc;
    cursor: not-allowed;
}
